import React, { useEffect, useState, useRef } from 'react';
import './App.css';
import './spelling-bee.css';
import { Level, LEFT_LEVELS, RIGHT_LEVELS, getCookie } from './scoring';

function sortLettersInWord(w: string) {
    return w.split('').sort().join('');
};

// taken from https://stackoverflow.com/a/57926311
// Hook
function useEventListener(eventName: string, handler: (e: any) => void, element = window){
  // Create a ref that stores handler
  const savedHandler = useRef();

  // Update ref.current value if handler changes.
  // This allows our effect below to always get latest handler ...
  // ... without us needing to pass it in effect deps array ...
  // ... and potentially cause effect to re-run every render.
  useEffect(() => {
    savedHandler.current = handler as any;
  }, [handler]);

  useEffect(
    () => {
      // Make sure element supports addEventListener
      // On
      const isSupported = element && element.addEventListener;
      if (!isSupported) return;

      // Create event listener that calls handler function stored in ref
      const eventListener = (event: any) => (savedHandler.current as any)(event);

      // Add event listener
      element.addEventListener(eventName, eventListener);

      // Remove event listener on cleanup
      return () => {
        element.removeEventListener(eventName, eventListener);
      };
    },
    [eventName, element] // Re-run if eventName or element changes
  );
};

let messageHideTimeout: number | undefined = undefined;

enum HiveShuffleState {
  NONE = 0,
  FADE_OUT = 1,
  SHUFFLING = 2,
  FADE_IN = 3,
}

function shuffleStr(s: string): {word: string, permutation: number[]} {
  let word = '';
  let startingNumbers: number[] = [];
  for (let i = 0; i < s.length; i++) {
    startingNumbers.push(i);
  }
  const permutation: number[] = [];
  for (let i = s.length; i > 0; i--) {
    let r = Math.floor(Math.random() * i);
    word = word + s[r];
    permutation.push(startingNumbers[r]);
    s = s.slice(0, r) + s.slice(r + 1);
    startingNumbers.splice(r, 1);
  }
  return {word, permutation};
}

interface SbMessageState {
  message: string;
  isError: boolean;
  points: number;
  isPangram: boolean;
}

function SbControls(props: {
  levelI: number,
  level: Level,
  nonce: string,
  foundWords: string[],
  isRightSide: boolean,
  onEnterWord: (word: string, wordScore: number) => void,
  onReset: () => void;
  onJumpToLevel: () => void;
}) {
  const {centerLetter, outerLetters} = props.level;
  const [shuffledOuterLetters, setShuffledOuterLetters] = useState(shuffleStr(outerLetters));
  const [inputLetters, setInputLetters] = useState('');
  const [messageState, setMessageState] = useState<SbMessageState|undefined>(undefined);
  const [shuffleAnimState, setShuffleAnimState] = useState<HiveShuffleState>(HiveShuffleState.NONE);

  useEffect(() => {
    if (sortLettersInWord(outerLetters) !== sortLettersInWord(shuffledOuterLetters.word)) {
      let newShuffledWord = '';
      for (let i = 0; i < 6; i++) {
        newShuffledWord += outerLetters[shuffledOuterLetters.permutation[i]];
      }
      setShuffledOuterLetters({word: newShuffledWord, permutation: shuffledOuterLetters.permutation});
    }
  }, [outerLetters, shuffledOuterLetters.word, shuffledOuterLetters.permutation]);

  let inputDivContents: JSX.Element | JSX.Element[] = <span>&nbsp;</span>;
  let inputContentDivClass = 'sb-hive-input-content';
  if (inputLetters.length > 0) {
    inputContentDivClass += ' non-empty';
    inputDivContents = [];
    for (let i = 0; i < inputLetters.length; i++) {
      let classes = inputLetters[i] === centerLetter ? 'sb-input-bright' : outerLetters.indexOf(inputLetters[i]) === -1 ? 'sb-input-invalid' : '';
      inputDivContents.push(<span className={classes}>{inputLetters[i]}</span>);
    }
  }

  const handleClickLetter = (l: string) => {
    setInputLetters((inputLetters) => {
      if (messageState && messageState.isError) {
        return l;
      }
      return inputLetters + l;
    });
    if (messageState && messageState.isError) {
      setMessageState(undefined);
      window.clearTimeout(messageHideTimeout);
    }
  }

  const handleDelete = () => {
    setInputLetters((inputLetters) => {
      if (inputLetters.length > 0) {
        return inputLetters.slice(0, inputLetters.length - 1);
      }
      return '';
    });
  }

  const handleShuffle = () => {
    if (shuffleAnimState !== HiveShuffleState.NONE) {
      return;
    }

    setShuffleAnimState(HiveShuffleState.FADE_OUT);
    setTimeout(() => {
      setShuffledOuterLetters(shuffleStr(outerLetters));
      setShuffleAnimState(HiveShuffleState.FADE_IN);
      setTimeout(() => {
        setShuffleAnimState(HiveShuffleState.NONE);
      }, 300);
    }, 300);
  }

  const flashSuccessMessage = (word: string, wordScore: number, isPangram: boolean) => {
    if (messageHideTimeout) window.clearTimeout(messageHideTimeout);

    setMessageState({
      message: isPangram ? "Pangram!" : word.length >= 7 ? "Awesome!" : word.length >= 5 ? "Nice!" : "Good!",
      points: wordScore,
      isPangram: isPangram,
      isError: false,
    });

    messageHideTimeout = window.setTimeout(() => {
      setMessageState(undefined);
    }, 900);
  }

  const flashErrorMessage = (message: any) => {
    if (messageHideTimeout) window.clearTimeout(messageHideTimeout);

    setMessageState({
      message,
      points: 0,
      isPangram: false,
      isError: true,
    });

    messageHideTimeout = window.setTimeout(() => {
      setMessageState(undefined);
      setInputLetters('');
    }, 1100);
  }

  const handleEnter = () => {
    const word = inputLetters;
    if (word.length > 0) {
      // Calculate isPangram and wordScore before calling evaluateWord, since
      // the latter can mutate state.
      let isPangram = props.level.isPangram(word);
      let wordScore = props.level.getScore(word);
      let resultPromise = props.level.evaluateWord(
        word, props.foundWords, props.nonce, props.levelI,
        props.isRightSide ? "right" : "left");
      resultPromise.then(result => {
        if (result !== "ok") {
          flashErrorMessage(result);
          return;
        } else {
          setInputLetters('');
          flashSuccessMessage(word, wordScore, isPangram);
          props.onEnterWord(word, wordScore);
        }
      });
    }
  }

  useEventListener('keydown', (e) => {
    const key = e.key;
    if (key.length === 1 && key.match(/[a-z]/i)) {
      handleClickLetter(key);
    } else if (key === 'Backspace') {
      handleDelete();
    } else if (key === 'Enter') {
      handleEnter();
    } else if (key === ' ') {
      handleShuffle();
    }
  });

  let sbMessageBoxClasses = 'sb-message-box';
  let sbMessageContents = [];
  if (messageState != null) {
    sbMessageContents.push(<span className="sb-message">{messageState.message}</span>);
    if (messageState.isError) {
      sbMessageBoxClasses += ' error-message';
      inputContentDivClass += ' has-error';
    } else {
      sbMessageContents.push(<span className="sb-message-points">+{messageState.points}</span>);
      sbMessageBoxClasses += ' success-message';
      if (messageState.isPangram) {
        sbMessageBoxClasses += ' pangram-message';
      }
    }
  }

  const showJumpToLevelButton = (props.levelI > 0 || getMaxLevelFromLocalstorage(props.isRightSide) > 0);

  return (
    <div className="sb-controls-box">
      <div className="sb-controls">
        <div className={sbMessageBoxClasses}>{sbMessageContents}</div>
        {/* <div className="sb-message-box"></div> */}
        {/* <div className="sb-message-box success-message pangram-message">
          <span className="sb-message">Pangram!</span>
          <span className="sb-message-points">+14</span>
        </div> */}
        {/* <div className="sb-message-box success-message">
          <span className="sb-message">Nice!</span>
          <span className="sb-message-points">+5</span>
        </div> */}
        <div className='sb-hive-input'>
          <span className={inputContentDivClass} style={{fontSize: 32}}>
            {inputDivContents}
          </span>
        </div>
        <div className="sb-hive">
          <Hive
            centerLetter={centerLetter}
            outerLetters={shuffledOuterLetters.word}
            shuffleState={shuffleAnimState}
            onClickLetter={handleClickLetter}
          />
        </div>
        <div className="hive-actions">
          <div className="hive-action hive-action__delete sb-touch-button" onClick={handleDelete}>Delete</div>
          <div className="hive-action hive-action__shuffle sb-touch-button" onClick={handleShuffle}></div>
          <div className="hive-action hive-action__submit sb-touch-button" onClick={handleEnter}>Enter</div>
        </div>
        <div className="hive-actions" style={{marginTop: 16}}>
          {!showJumpToLevelButton ? <div style={{flex: '0.5'}}></div> : undefined}
          <div className="hive-action hive-action__delete sb-touch-button" onClick={props.onReset}>Reset Level</div>
          {showJumpToLevelButton ? <div className="hive-action hive-action__delete sb-touch-button" onClick={props.onJumpToLevel}>Jump to Level</div> : undefined}
          {!showJumpToLevelButton ? <div style={{flex: '0.5'}}></div> : undefined}
        </div>
      </div>
    </div>
  );
}

function SbStatus(props: {
  level: Level
  rankName: string,
  rankNumber: number,
  score: number,
  words: string[],
  onShowRankings: () => void,
}) {

  const progressDots = [];
  for (let i = 0; i <= 8; i++) {
    let c = "sb-progress-dot";
    if (i < props.rankNumber) {
      c += " completed";
    }
    progressDots.push(<span className={c}></span>);
  }

  const wordlistItems = [];
  const sortedWords = [...props.words];
  if (props.level.shouldSort()) {
    sortedWords.sort();
  }
  for (let word of sortedWords) {
    wordlistItems.push(<li><span className="sb-anagram">{word}</span></li>);
  }

  let markerClasses = "sb-progress-marker";
  if (props.rankNumber >= 8) {
    markerClasses += " final";
  }

  const [numPaginationPages, setNumPaginationPages] = useState(1);
  const [paginationPage, setPaginationPage] = useState(0);
  const resizeHandler = () => {
    let numPages = 0;
    if (paginationPage < numPaginationPages) {
      const ce = document.getElementsByClassName("sb-wordlist-items-pag")[0];
      const le = document.querySelector(".sb-wordlist-items-pag > li");
      if (ce && le) {
        const containerHeight = ce.getBoundingClientRect().height - 24;
        const elementHeight = le.getBoundingClientRect().height + 8;
        const elementsPerColumn = Math.floor(containerHeight / elementHeight);
        numPages = Math.ceil((props.words.length / 3) / elementsPerColumn);
      }
    }
    setNumPaginationPages(numPages);
    if (paginationPage >= numPages) {
      setPaginationPage(0);
    }
  };
  useEffect(resizeHandler);
  useEventListener('resize', resizeHandler);

  let paginationKebob: undefined | JSX.Element = undefined;
  if (numPaginationPages > 1) {
    paginationKebob = <div className="sb-kebob">
      <button type="button" className={"sb-bob-arrow sb-bob-prev" + (paginationPage > 0 ? " active" : "")} onClick={() => {
        if (paginationPage > 0) {
          setPaginationPage((i) => i - 1);
          const e = document.getElementsByClassName("sb-wordlist-scroll-anchor")[paginationPage - 1];
          if (e) {
            e.scrollIntoView();
          }
        }
      }}>
        <span className="sb-bob-text">Prev</span>
      </button>
      {(() => {
        let a = [];
        for (let i = 0; i < numPaginationPages; i++) {
          a.push(<div className={"sb-bob" + (paginationPage === i ? " active" : "")}></div>);
        }
        return a;
      })()}
      <button type="button" className={"sb-bob-arrow sb-bob-next" + (paginationPage < (numPaginationPages - 1) ? " active" : "")} onClick={() => {
        if (paginationPage < numPaginationPages - 1) {
          setPaginationPage((i) => i + 1);
          const e = document.getElementsByClassName("sb-wordlist-scroll-anchor")[paginationPage + 1];
          if (e) {
            e.scrollIntoView();
          }
        }
      }}>
        <span className="sb-bob-text">Next</span>
      </button>
    </div>;
  }

  return (
    <div className="sb-status-box">
      <div className="sb-progress-box">
        <span role="presentation">
          <div className="sb-progress" title="Click to see this level’s ranks" onClick={props.onShowRankings}>
            <h4 className="sb-progress-rank">{props.rankName}</h4>
            <div className="sb-progress-line">
              <div className="sb-progress-dots">
                {progressDots}
              </div>
              <div className={markerClasses} style={{left: (props.rankNumber * 12.5) + "%"}}><span className="sb-progress-value">{props.score}</span></div>
            </div>
          </div>
        </span>
      </div>
      <div className="sb-wordlist-box">
        <div className="sb-wordlist-heading">
          <div className="sb-wordlist-heading-wrap sb-touch-button">
            <div className="sb-wordlist-summary">You have found {props.words.length === 1 ? '1 word' : `${props.words.length} words`}</div>
            {/* this is a foldout menu for the mobile web interface, probably won't implement this */}
            {/* <div className="sb-recent-words-wrap">
              <ul className="sb-recent-words sb-has-words">
                <li><span className="sb-anagram">ligh<span className="sb-anagram-key">t</span>ed</span></li>
              </ul>
            </div> */}
            {/* <div className="sb-toggle-expand"><span className="sb-toggle-icon"></span></div> */}
          </div>
        </div>
        <div className="sb-wordlist-drawer paginated">
          <div className="sb-wordlist-window paginated">
            <div className="sb-wordlist-pag">
              {(() => {
                let a = [];
                for (let i = 0; i < numPaginationPages; i++) {
                  a.push(<div className="sb-wordlist-scroll-anchor" style={{left: (i * 100) + "%"}}></div>);
                }
                return a;
              })()}
              <ul className="sb-wordlist-items-pag">
                {wordlistItems}
              </ul>
            </div>
            {paginationKebob}
          </div>
        </div>
      </div>
    </div>
  );
}


function Hive(props: {
  centerLetter: string,
  outerLetters: string,
  shuffleState: HiveShuffleState,
  onClickLetter: (l: string) => void,
}) {
  let hiveClasses = "hive";
  if (props.shuffleState === HiveShuffleState.FADE_OUT) {
    hiveClasses += " fade-out";
  } else if (props.shuffleState === HiveShuffleState.SHUFFLING) {
    hiveClasses += " shuffling";
  } else if (props.shuffleState === HiveShuffleState.FADE_IN) {
    hiveClasses += " fade-in";
  }

  const [mousedowns, setMousedowns] = useState([false,false,false,false,false,false,false]);

  const setHexMouseDown = (n: number, b: boolean) => {
    setMousedowns((ms) => {
      const c = [...ms];
      c[n] = b;
      return c;
    });
  }

  const makeHex = (isCenter: boolean, n: number) => {
    return <svg
      onClick={() => props.onClickLetter(isCenter ? props.centerLetter[0] : props.outerLetters[n])}
      onMouseDown={() => setHexMouseDown(n, true)}
      onMouseUp={() => setHexMouseDown(n, false)}
      className={"hive-cell" + (isCenter ? " center" : " outer")}
      viewBox="0 0 120 103.92304845413263"
    >
      <polygon className={"cell-fill" + (mousedowns[n] ? " push-active" : "")} points="0,51.96152422706631 30,0 90,0 120,51.96152422706631 90,103.92304845413263 30,103.92304845413263" stroke="white" stroke-width="7.5">
      </polygon>
      <text className="cell-letter" x="50%" y="50%" dy="11.25%">{isCenter ? props.centerLetter[0] : props.outerLetters[n]}</text>
    </svg>
  };

  const hexes: JSX.Element[] = [];
  hexes.push(makeHex(true, 6));
  hexes.push(makeHex(false, 0));
  hexes.push(makeHex(false, 1));
  hexes.push(makeHex(false, 2));
  hexes.push(makeHex(false, 3));
  hexes.push(makeHex(false, 4));
  hexes.push(makeHex(false, 5));

  return (
    <div className={hiveClasses}>
      {hexes}
    </div>
  )
}

function scoreToRank(score: number, ranks: [number, string][]): {rankName: string, rankNumber: number} {
  let rankNumber = -1;
  let rankName = '';
  for (let [rnum, rname] of ranks) {
    if (rnum <= score) {
      rankNumber += 1;
    } else {
      break;
    }
    rankName = rname;
  }
  return {rankName, rankNumber}
}

enum GeniusMomentState {
  GAME_VISIBLE,
  GAME_FLYOUT,
  GENIUS_FLYIN,
  GENIUS_VISIBLE,
  GENIUS_FLYOUT,
  GAME_FLYIN,
}

function saveProgressToLocalstorage(levelI: number, level: Level, score: number, foundWords: string[], isRightSide: boolean, nonce: string) {
  const side = isRightSide ? 'right' : 'left';
  window.localStorage.setItem(`sytycs-level-${side}`, levelI.toString());
  window.localStorage.setItem(`sytycs-score-${side}`, score.toString());
  window.localStorage.setItem(`sytycs-foundwords-${side}`, foundWords.join(','));
  window.localStorage.setItem(`sytycs-letters-${side}`, level.centerLetter + level.outerLetters);
  window.localStorage.setItem(`sytycs-nonce-${side}`, nonce)

  let maxLevelReached = 0;
  const s = window.localStorage.getItem(`sytycs-max-level-reached-${side}`);
  if (s && parseInt(s) > 0) {
    maxLevelReached = parseInt(s);
  }
  if (levelI > maxLevelReached) {
    window.localStorage.setItem(`sytycs-max-level-reached-${side}`, levelI.toString());
  }
}

function getLevelIFromLocalstorage(isRightSide: boolean): number {
  const side = isRightSide ? 'right' : 'left';
  const level = window.localStorage.getItem(`sytycs-level-${side}`);
  if (level) {
    return parseInt(level);
  }
  return 0;
}

function getScoreFromLocalstorage(isRightSide: boolean): number {
  const side = isRightSide ? 'right' : 'left';
  const score = window.localStorage.getItem(`sytycs-score-${side}`);
  if (score) {
    return parseInt(score);
  }
  return 0;
}

function getFoundWordsFromLocalstorage(isRightSide: boolean): string[] {
  const side = isRightSide ? 'right' : 'left';
  const foundWords = window.localStorage.getItem(`sytycs-foundwords-${side}`);
  if (foundWords) {
    return foundWords.split(',');
  }
  return [];
}

function getLettersFromLocalstorage(isRightSide: boolean): string | null {
  const side = isRightSide ? 'right' : 'left';
  const letters = window.localStorage.getItem(`sytycs-letters-${side}`);
  if (letters) {
    return letters;
  }
  return null;
}

function getMaxLevelFromLocalstorage(isRightSide: boolean): number {
  const side = isRightSide ? 'right' : 'left';
  let maxLevelReached = 0;
  const s = window.localStorage.getItem(`sytycs-max-level-reached-${side}`);
  if (s && parseInt(s) > 0) {
    maxLevelReached = parseInt(s);
  }
  return maxLevelReached;
}

function getNonceFromLocalstorage(isRightSide: boolean): string {
  const side = isRightSide ? 'right' : 'left';
  const s = window.localStorage.getItem(`sytycs-nonce-${side}`);
  if (s) {
    return s
  }
  return '';
}

function getAnswerFromLocalstorage() : string {
  const s = window.localStorage.getItem(`sytycs-answer`);
  if (s) {
    return s
  }
  return '';
}

function showRightSide(): boolean {
  return window.location.search.indexOf('right') > -1;
}

function App() {
  const isRightSide = showRightSide();
  const [nonce, setNonce] = useState(() => getNonceFromLocalstorage(isRightSide));
  const [levelI, setLevelI] = useState(() => getLevelIFromLocalstorage(isRightSide));
  const [score, setScore] = useState(() => getScoreFromLocalstorage(isRightSide));
  const [foundWords, setFoundWords] = useState<string[]>(() => getFoundWordsFromLocalstorage(isRightSide));
  const [rankingsModalOpen, setRankingsModalOpen] = useState(false);
  const [resetModalOpen, setResetModalOpen] = useState(false);
  const [jumpToLevelModalOpen, setJumpToLevelModalOpen] = useState(false);
  const [isModalClosing, setIsModalClosing] = useState(false);
  const [geniusMomentState, setGeniusMomentState] = useState(GeniusMomentState.GAME_VISIBLE);
  const [answer, setAnswer] = useState(() => getAnswerFromLocalstorage());

  const level = (isRightSide ? RIGHT_LEVELS : LEFT_LEVELS)[levelI];

  useEffect(() => {
    const lettersFromLocalstorage = getLettersFromLocalstorage(isRightSide); // window.localStorage.getItem("sytycs-letters");
    if (lettersFromLocalstorage && lettersFromLocalstorage.length === 7 && sortLettersInWord(level.outerLetters) !== sortLettersInWord(lettersFromLocalstorage.slice(1, 7))) {
      level.centerLetter = lettersFromLocalstorage[0];
      level.outerLetters = lettersFromLocalstorage.slice(1, 7);
      setFoundWords(foundWords.join(',').split(','));
    }

    const {rankNumber} = scoreToRank(score, level.ranks);
    if (rankNumber >= 8) {
      setGeniusMomentState(GeniusMomentState.GAME_FLYOUT);
      setTimeout(() => {
        setGeniusMomentState(GeniusMomentState.GENIUS_FLYIN);
        setTimeout(() => {
          setGeniusMomentState(GeniusMomentState.GENIUS_VISIBLE);
        }, 600);
      }, 600);
    }
  }, []);

  useEffect(() => {
    saveProgressToLocalstorage(levelI, level, score, foundWords, isRightSide, nonce);
  }, [levelI, score, foundWords]);

  const {rankName, rankNumber} = scoreToRank(score, level.ranks);

  // useEffect(() => {
  //   setGeniusMomentState(GeniusMomentState.GAME_FLYOUT);
  //   setTimeout(() => {
  //     setGeniusMomentState(GeniusMomentState.GENIUS_FLYIN);
  //     setTimeout(() => {
  //       setGeniusMomentState(GeniusMomentState.GENIUS_VISIBLE);
  //     }, 600);
  //   }, 600);
  // }, []);

  const handleEnterWord = (word: string, wordScore: number) => {
    setFoundWords((foundWords) => {
      return [...foundWords, word];
    });
    const {rankNumber} = scoreToRank(wordScore + score, level.ranks);
    setScore((score) => wordScore + score);
    if (rankNumber >= 8) {
      setGeniusMomentState(GeniusMomentState.GAME_FLYOUT);
      setTimeout(() => {
        setGeniusMomentState(GeniusMomentState.GENIUS_FLYIN);
        setTimeout(() => {
          setGeniusMomentState(GeniusMomentState.GENIUS_VISIBLE);
        }, 600);
      }, 600);
    }
  }

  const handleShowRankings = () => {
    setRankingsModalOpen(true);
  }

  const handleCloseModal = () => {
    setIsModalClosing(true);
    setTimeout(() => {
      setRankingsModalOpen(false);
      setResetModalOpen(false);
      setJumpToLevelModalOpen(false);
      setIsModalClosing(false);
    }, 100);
  }

  let geniusFrameClasses = "pz-moment__frame pz-moment__congrats";
  if ([GeniusMomentState.GENIUS_FLYIN, GeniusMomentState.GENIUS_FLYOUT, GeniusMomentState.GENIUS_VISIBLE].includes(geniusMomentState)) {
    geniusFrameClasses += " on-stage";
  }
  if (geniusMomentState === GeniusMomentState.GENIUS_FLYIN) {
    geniusFrameClasses += " fly-in";
  }
  if (geniusMomentState === GeniusMomentState.GENIUS_FLYOUT) {
    geniusFrameClasses += " fly-out";
  }
  let gameFrameClasses = "pz-game-screen pz-moment__frame";
  if ([GeniusMomentState.GAME_FLYIN, GeniusMomentState.GAME_FLYOUT, GeniusMomentState.GAME_VISIBLE].includes(geniusMomentState)) {
    gameFrameClasses += " on-stage";
  }
  if (geniusMomentState === GeniusMomentState.GAME_FLYIN) {
    gameFrameClasses += " fly-in";
  }
  if (geniusMomentState === GeniusMomentState.GAME_FLYOUT) {
    gameFrameClasses += " fly-out";
  }

  const handleNextLevel = () => {
    if (levelI < LEFT_LEVELS.length - 1) {
      if (geniusMomentState !== GeniusMomentState.GENIUS_VISIBLE) {
        return;
      }
      setGeniusMomentState(GeniusMomentState.GENIUS_FLYOUT);
    }
    setTimeout(() => {
      const data = {
        "level" : levelI,
        "side" : (isRightSide ? "right" : "left"),
        "nonce" : nonce,
        "word_list" : foundWords,
      };
      let csrftoken = getCookie('csrftoken');
      fetch("/puzzle/so-you-think-you-can-count/claim_genius",
        {
          method: "POST",
          body: JSON.stringify(data),
          headers: { "X-CSRFToken": csrftoken },
        })
        .then(response => response.json())
        .then(response_data => {
          const is_genius = response_data['is_genius'];
          if (is_genius) {
            if (levelI < LEFT_LEVELS.length - 1) {
              setNonce(response_data['next_level_nonce']);
              setFoundWords([]);
              setScore(0);
              setLevelI((li) => li + 1);
              setGeniusMomentState(GeniusMomentState.GAME_FLYIN);
              setTimeout(() => {
                setGeniusMomentState(GeniusMomentState.GAME_VISIBLE);
              }, 600);
            } else {
              setAnswer(response_data['answer']);
            }
          }
        });
    }, 600);
  }

  const handleShowResetModal = () => {
    setResetModalOpen(true);
  }

  const handleShowJumpToLevelModal = () => {
    setJumpToLevelModalOpen(true);
  }

  const handleReset = () => {
    handleCloseModal();
    level.reset();
    // window.localStorage.setItem("sytycs-letters", level.centerLetter + level.outerLetters);
    // window.localStorage.setItem("sytycs-score", "0");
    // window.localStorage.setItem("sytycs-foundwords", "");
    setFoundWords([]);
    setScore(0);
  }

  const handleJumpToLevel = (newLevelI: number) => {
    handleCloseModal();
    const level = (isRightSide ? RIGHT_LEVELS : LEFT_LEVELS)[newLevelI];
    level.reset();
    // window.localStorage.setItem("sytycs-letters", level.centerLetter + level.outerLetters);
    // window.localStorage.setItem("sytycs-score", "0");
    // window.localStorage.setItem("sytycs-foundwords", "");
    // window.localStorage.setItem("sytycs-level", "0");
    setLevelI(newLevelI);
    setFoundWords([]);
    setScore(0);
  }

  const geniusContinueButton = <div onClick={handleNextLevel} className="pz-moment__button-wrapper slide-up" style={{transitionDelay: "800ms"}}><button type="button" className="pz-moment__button primary">Continue</button></div>;

  const geniusModalContent = (
    <div className="pz-moment__content sequence-animation">
      {/* The transition that fades these in one at a time is fucked right now and I don't know why */}
      <div className="pz-moment__icon large spelling-bee-smarty slide-up" style={{transitionDelay: "200ms"}}></div>
      <h1 className="pz-moment__title large slide-up" style={{transitionDelay: "400ms"}}>Genius</h1>
      <h1 className="pz-moment__description default slide-up" style={{transitionDelay: "600ms"}}>You reached the highest rank, with <em>{foundWords.length}&nbsp;words</em> and <em>{score}&nbsp;points</em>.</h1>
      {/* <div className="pz-moment__button-wrapper slide-up" style={{transitionDelay: "800ms"}}><button type="button" className="pz-moment__button primary">Keep playing</button><button type="button" className="pz-moment__button secondary">More puzzles</button></div> */}
      {geniusContinueButton}
      {answer !== "" ? <h1 className="pz-moment__description default slide-up">Congratulations! The answer to this puzzle is <em>{answer}</em>.</h1> : undefined}
    </div>
  );

  return (
    <div className="App pz-page pz-desktop pz-spelling-bee-wordlist">
      <div id="spelling-bee-container" className="pz-section">
        <div className="pz-game-wrapper" id="js-hook-game-wrapper" style={{backgroundColor: "white", position: "static", overflow: "visible", minHeight: 0}}>
          <div id="portal-game-moments">
            <section className={geniusFrameClasses} id="js-hook-pz-moment__congrats" style={{visibility: "visible", position: "static", top: 0}}>
              <div className="pz-moment" style={{backgroundColor: "white"}}>
                <div className="pz-moment__container alternate">
                  <button type="button" className="pz-moment__close" aria-label="Close" onClick={handleNextLevel}>×</button>
                  {[GeniusMomentState.GENIUS_FLYIN, GeniusMomentState.GENIUS_VISIBLE, GeniusMomentState.GENIUS_FLYOUT].includes(geniusMomentState) ? geniusModalContent : null}
                </div>
              </div>
            </section>
          </div>
          <div className={gameFrameClasses} id="js-hook-pz-moment__game" style={{visibility: "visible", position: "static", top: 0}}>
            <Modals
              level={level}
              levelI={levelI}
              rankingsOpen={rankingsModalOpen}
              resetOpen={resetModalOpen}
              jumpToLevelOpen={jumpToLevelModalOpen}
              isClosing={isModalClosing}
              isRightSide={isRightSide}
              onCloseModal={handleCloseModal}
              onReset={handleReset}
              onJumpToLevel={handleJumpToLevel}
            />
            <div id="pz-game-root" className="pz-game-field">
              <div className="sb-content-box">
                <SbControls levelI={levelI} level={level} nonce={nonce} onEnterWord={handleEnterWord} foundWords={foundWords} isRightSide={isRightSide} onReset={handleShowResetModal} onJumpToLevel={handleShowJumpToLevelModal} />
                <SbStatus level={level} rankName={rankName} rankNumber={rankNumber} score={score} words={foundWords} onShowRankings={handleShowRankings} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

function Modals(props: {
  level: Level,
  levelI: number,
  isClosing: boolean,
  rankingsOpen: boolean,
  resetOpen: boolean,
  jumpToLevelOpen: boolean,
  isRightSide: boolean,
  onCloseModal: () => void,
  onReset: () => void,
  onJumpToLevel: (n: number) => void,
}) {
  const isOpen = props.rankingsOpen || props.resetOpen || props.jumpToLevelOpen /* || other modals */;
  let modalSystemClasses = "sb-modal-system";
  if (isOpen) {
    modalSystemClasses += " sb-modal-open";
  }
  if (props.isClosing) {
    modalSystemClasses += " sb-modal-closing";
  }

  const rankingsModal = (
    <div role="button" className="sb-modal-frame left-aligned ranks" onClick={(e) => {e.stopPropagation()}}>
      <div role="button" className="sb-modal-close" onClick={(e) => {props.onCloseModal(); e.stopPropagation()}}>×</div>
      <div className="sb-modal-content">
        <div className="sb-modal-header">
          <h3 className="sb-modal-title">Level {props.levelI + 1}</h3>
          <p className="sb-modal-message">
            Ranks are based on a percentage of possible points in a puzzle. The minimum scores to reach each rank for this level’s are:
          </p>
        </div>
        <div className="sb-modal-body">
          <ol className="sb-modal-list">
            { props.level.ranks.map(([a, b]) => <li><span className="sb-modal-rank">{b}</span> ({a})</li>) }
          </ol>
          {/* <p className="sb-modal-message">Think we missed a word?</p> */}
          { props.level.getModalMessage() }
        </div>
      </div>
    </div>
  );

  const resetModal = (
    <div role="button" className="sb-modal-frame left-aligned ranks" onClick={(e) => {e.stopPropagation()}}>
      <div role="button" className="sb-modal-close" onClick={(e) => {props.onCloseModal(); e.stopPropagation()}}>×</div>
      <div className="sb-modal-content">
        <div className="sb-modal-header">
          <h3 className="sb-modal-title">Reset Level?</h3>
          <p className="sb-modal-message">
            Are you sure you want to reset this level? This will clear all the words you&rsquo;ve found and reset your score to zero (but you will stay on level {props.levelI + 1}).
          </p>
        </div>
        <div className="sb-modal-body">
          <div className="pz-moment__button-wrapper" style={{marginTop: 12}}>
            <button onClick={props.onReset} type="button" className="pz-moment__button primary">Reset</button>
            <button onClick={props.onCloseModal} type="button" className="pz-moment__button secondary" style={{marginBottom: 0}} >Cancel</button>
          </div>
        </div>
      </div>
    </div>
  );

  const jumpToLevelButtons: JSX.Element[][] = [[], [], []];
  for (let i = 0; i < Math.max(props.levelI, getMaxLevelFromLocalstorage(props.isRightSide)) + 1; i++) {
    jumpToLevelButtons[Math.floor(i/3)].push(
      <button style={{marginBottom: 0, minWidth: '8.7em'}} onClick={() => props.onJumpToLevel(i)} type="button" className="pz-moment__button primary">Level {i + 1}</button>
    );
  }

  const jumpToLevelModal = (
    <div role="button" className="sb-modal-frame left-aligned ranks" onClick={(e) => {e.stopPropagation()}}>
      <div role="button" className="sb-modal-close" onClick={(e) => {props.onCloseModal(); e.stopPropagation()}}>×</div>
      <div className="sb-modal-content">
        <div className="sb-modal-header">
          <h3 className="sb-modal-title">Jump to Level</h3>
          <p className="sb-modal-message">
            Reset the game to the start of any previously visited level. This will cause all of your progress on this level to be lost.
          </p>
        </div>
        <div className="sb-modal-body">
          {jumpToLevelButtons.map((l) =>
            <div className="pz-moment__button-wrapper" style={{marginTop: 12, flexDirection: 'row', marginBottom: 0, justifyContent: 'left'}}>
              {l}
            </div>
          )}
          <div className="pz-moment__button-wrapper" style={{marginTop: 12}}>
            {/* {jumpToLevelButtons} */}
            {/* <button onClick={props.onReset} type="button" className="pz-moment__button primary">Reset</button> */}
            <button onClick={props.onCloseModal} type="button" className="pz-moment__button secondary" style={{marginBottom: 0}} >Cancel</button>
          </div>
        </div>
      </div>
    </div>
  );

  return (<div id="portal-game-modals">
    <div className={modalSystemClasses}>
      <div role="presentation" className="sb-modal-scrim">
        <div className="sb-modal-wrapper" onClick={props.onCloseModal}>
          { props.rankingsOpen ? rankingsModal : undefined }
          { props.resetOpen ? resetModal : undefined }
          { props.jumpToLevelOpen ? jumpToLevelModal : undefined }
        </div>
      </div>
    </div>
  </div>);
}

export default App;
